import {Component, ViewEncapsulation, OnInit} from '@angular/core';
import {Stock} from '../../domain/stock';

@Component({
    selector: 'view-stcoks',
    templateUrl: './view-stocks.component.html',
    encapsulation:ViewEncapsulation.None,
    styleUrls:['./view-stocks.component.css']
})

export class ViewStocksComponent  implements OnInit {
    private stockList:Array<Stock> =[];

    ngOnInit() {
        this.initialize();        
    }
    private initialize():void{
        this.stockList = [
            new Stock("AAPL",12),new Stock("GOOG",15)
        ];
    }
      
}


