import { Component,OnInit } from '@angular/core';
import { Portfolio } from '../../domain/portfolio';
import { Order } from '../../domain/order';
import { Stock } from '../../domain/stock';

@Component({
	selector:'portfolio',
	templateUrl:'./portfolio.component.html',
	styleUrls:['./portfolio.component.css']
})
export class PortfolioComponent implements OnInit {
	private porfolioList:Array<Portfolio>=[];
	ngOnInit(){
		this.initialize();
	
	}
	constructor() {
		// code...
	}

	initialize(){
		this.porfolioList=[new Portfolio("Portfolio 1",[
				new Order("Apple 100",new Stock("APPL",12),100,11),
				new Order("Apple 200",new Stock("APPL",12),200,10),
				new Order("Apple 300",new Stock("APPL",12),300,12)
			])];
	}
}


