import  { ViewStocksComponent,PortfolioComponent,OrdersComponent } from './components/index'

export const routes = [
	{path: 'viewStocks', component: ViewStocksComponent},
    {path: 'portfolios', component: PortfolioComponent},
    {path: 'orders', component: OrdersComponent},
    {path: '', redirectTo:'viewStocks', pathMatch:'full'},
    {path: '**', redirectTo:'portfolios', pathMatch:'full'}
];


